.glide {
  width: 100%;
  flex-direction: column;
  align-items: center;
  padding: 0 10px;
  display: flex;
}

.glide__slides {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .glide__slides {
    flex-flow: wrap;
    row-gap: 20px;
  }
}

.glide__slide {
  height: 500px;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.glide__slide:hover .glide__hover-box, .glide__slide:focus .glide__hover-box, .glide__slide:active .glide__hover-box {
  visibility: visible;
  opacity: 1;
}

@media screen and (min-width: 768px) {
  .glide__slide {
    width: calc(50% - 10px);
  }
}

@media screen and (min-width: 1024px) {
  .glide__slide {
    width: calc(33% - 10px);
  }

  .glide__slide:last-child {
    width: 100%;
    height: 350px;
  }

  .glide__slide:last-child .glide__decoration-box {
    width: 97%;
    height: 320px;
  }
}

.glide__slide + .glide__slide {
  margin-top: 30px;
}

@media screen and (min-width: 768px) {
  .glide__slide + .glide__slide {
    margin-top: 0;
  }
}

.glide__slide--new-buildings {
  background-image: url("new-buildings-background.09829c9f.jpg");
}

.glide__slide--private {
  background-image: url("private-house-background.a675c766.jpg");
}

.glide__slide--old-buildings {
  background-image: url("old-buildings-background.46abd80d.jpg");
}

.glide__slide--commercial {
  background-image: url("commercial-background.3104cedc.jpg");
}

.glide__decoration-box {
  width: 90%;
  height: 450px;
  border: 2px solid #fff;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.glide__title {
  text-align: center;
  color: #fff;
  -webkit-text-stroke: 1px #a74d4a;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 32px;
  font-weight: 600;
  line-height: 1.2;
}

.glide__hover-box {
  visibility: hidden;
  opacity: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: 18px;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  transition: visibility .3s ease-in, opacity .3s ease-in;
  position: absolute;
}

.glide__title--hover {
  -webkit-text-stroke: transparent;
  color: #121212;
  margin-bottom: 20px;
  font-size: 26px;
  font-weight: 600;
}

.glide__hover-text {
  text-align: center;
  word-break: keep-all;
  -webkit-hyphens: auto;
  hyphens: auto;
  color: #121212;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.2;
}

